<template>
  <div class="user-wrap">
    <van-nav-bar
      title="微信推送设置"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-form @submit="onSubmit">
      <van-cell center title="验券通知">
        <van-switch v-model="switchChecked1" slot="right-icon" size="20" active-color="#07c160" />
      </van-cell>
      <van-cell center title="打款通知">
        <van-switch v-model="switchChecked2" slot="right-icon" size="20" active-color="#07c160" />
      </van-cell>
      <van-cell center title="纠纷通知">
        <van-switch v-model="switchChecked3" slot="right-icon" size="20" active-color="#07c160" disabled />
      </van-cell>
      <van-cell center title="任务提醒">
        <van-switch v-model="switchChecked4" slot="right-icon" size="20" active-color="#07c160" />
      </van-cell>
      <div style="margin: 16px;">
        <van-button round block type="primary" native-type="submit">
          确定
        </van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
export default {
  name: 'msgSetting',
  components: {
  },
  data () {
    return {
      switchChecked1: true,
      switchChecked2: true,
      switchChecked3: true,
      switchChecked4: true
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    onClickLeft () {
      this.$router.push({ path: '/user' })
    },
    onSubmit(values) {
      console.log('submit', values);
    }
  }
}
</script>



